import React from 'react';
import {
  Container,
  Typography,
  Box,
  Grid,
  Card,
  CardContent,
  CardMedia,
} from '@mui/material';

const About = () => {
  const teamMembers = [
    {
      name: 'Sarah Johnson',
      role: 'Founder & Tea Master',
      image: 'https://images.unsplash.com/photo-1573497019940-1c28c88b4f3e?ixlib=rb-4.0.3',
      bio: 'With over 15 years of experience in tea blending, Sarah brings her passion for creating the perfect cup.',
    },
    {
      name: 'Michael Chen',
      role: 'Head of Operations',
      image: 'https://images.unsplash.com/photo-1560250097-0b93528c311a?ixlib=rb-4.0.3',
      bio: 'Michael ensures that every aspect of our tea production meets the highest standards of quality.',
    },
    {
      name: 'Emma Rodriguez',
      role: 'Customer Experience',
      image: 'https://images.unsplash.com/photo-1573496359142-b8d87734a5a2?ixlib=rb-4.0.3',
      bio: 'Emma leads our customer service team, making sure every tea lover has an exceptional experience.',
    },
  ];

  return (
    <Box>
      {/* Hero Section */}
      <Box
        sx={{
          bgcolor: 'primary.main',
          color: 'white',
          py: 8,
          textAlign: 'center',
        }}
      >
        <Container>
          <Typography variant="h2" component="h1" sx={{ mb: 2, fontFamily: 'Playfair Display' }}>
            Our Story
          </Typography>
          <Typography variant="h5" sx={{ maxWidth: '800px', mx: 'auto' }}>
            Crafting exceptional tea experiences since 2010
          </Typography>
        </Container>
      </Box>

      {/* Mission Section */}
      <Container sx={{ py: 8 }}>
        <Grid container spacing={6}>
          <Grid item xs={12} md={6}>
            <Typography variant="h4" component="h2" sx={{ mb: 3, fontFamily: 'Playfair Display' }}>
              Our Mission
            </Typography>
            <Typography variant="body1" paragraph>
              At TEVA, we believe that every cup of tea should be a moment of pure enjoyment. Our mission is to bring the finest tea leaves from around the world to your cup, while maintaining the highest standards of quality and sustainability.
            </Typography>
            <Typography variant="body1" paragraph>
              We work directly with tea farmers who share our commitment to ethical practices and environmental stewardship. Each blend is carefully crafted to ensure a perfect balance of flavor, aroma, and health benefits.
            </Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Box
              component="img"
              src="https://images.unsplash.com/photo-1576092768241-dec231879fc3?ixlib=rb-4.0.3"
              alt="Tea plantation"
              sx={{
                width: '100%',
                height: '400px',
                objectFit: 'cover',
                borderRadius: 2,
              }}
            />
          </Grid>
        </Grid>
      </Container>

      {/* Team Section */}
      <Box sx={{ bgcolor: 'grey.100', py: 8 }}>
        <Container>
          <Typography variant="h4" component="h2" sx={{ mb: 4, textAlign: 'center', fontFamily: 'Playfair Display' }}>
            Meet Our Team
          </Typography>
          <Grid container spacing={4}>
            {teamMembers.map((member) => (
              <Grid item key={member.name} xs={12} sm={6} md={4}>
                <Card>
                  <CardMedia
                    component="img"
                    height="300"
                    image={member.image}
                    alt={member.name}
                  />
                  <CardContent>
                    <Typography variant="h6" component="h3" gutterBottom>
                      {member.name}
                    </Typography>
                    <Typography variant="subtitle1" color="primary" gutterBottom>
                      {member.role}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {member.bio}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default About; 